@import './variables.scss';
@import './libs/normalize.scss';

@import './components/all.scss';

body {
  background-color: $background;
  padding: 30px;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;

  @media (max-width: 520px) {
    margin: 20px auto;
    width: 100%;
  }
}

.content {
  padding: 40px 0;

  &__error-info {
    margin: 60px auto;
    width: 580px;
    text-align: center;

    h2 {
      font-size: 30px;
      font-weight: 700;
      margin-bottom: 10px;
    }

    p {
      font-size: 26px;
    }
  }

  .fullpizza {
    width: 150px;
    height: 150px;
  }

  &__title {
    margin: 35px 0;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-column-gap: 11px;

    @media (max-width: 1400px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 1260px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 730px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 1260px) {
      flex-direction: column;

      .categories {
        margin-bottom: 30px;
        width: 100%;
        overflow: auto;

        ul {
          width: 785px;
        }
      }
    }
  }

  @media (max-width: 740px) {
    padding: 20px 0;
  }
}

.container {
  width: $container-width;
  margin: 0 auto;

  &--cart {
    max-width: 820px;
    margin: 90px auto;

    .content__title {
      margin: 0;
    }

    @media (max-width: 740px) {
      margin: 30px auto;
    }

    @media (max-width: 520px) {
      margin: 15px auto;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }

    @media (max-width: 740px) {
      font-size: 28px;

      svg {
        width: 25px;
        height: 25px;
      }
    }

    @media (max-width: 520px) {
      font-size: 24px;
      margin-right: 5px;
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }

    @media (max-width: 740px) {
      span {
        font-size: 16px;
      }
    }

    @media (max-width: 520px) {
      span {
        font-size: 14px;
      }
    }
  }

  .content__items {
    display: flex;
    flex-direction: column;
  }

  &__item {
    display: flex;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;

    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;
      width: 10%;

      img {
        width: 80px;
        height: 80px;
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 40%;

      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 13%;

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }
      }

      b {
        font-size: 22px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 33%;

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 4%;

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }

    @media (max-width: 740px) {
      &-count {
        flex-direction: column;
      }

      &-img {
        margin-right: 40px;
      }

      &-info {
        h3 {
          font-size: 18px;
          line-height: 1;
        }

        p {
          font-size: 16px;
        }
      }
    }

    @media (max-width: 520px) {
      &-count {
        b {
          font-size: 20px;
        }
      }

      &-img {
        margin-right: 35px;

        img {
          width: 60px;
          height: 60px;
        }
      }

      &-info {
        h3 {
          font-size: 16px;
        }

        p {
          font-size: 14px;
        }
      }

      &-price {
        b {
          font-size: 18px;
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
      }
    }

    @media (max-width: 740px) {
      margin: 30px 0;

      &-details {
        span {
          font-size: 20px;
        }
      }

      &-buttons {
        margin-top: 30px;

        .go-back-btn {
          width: 180px;
        }

        .pay-btn {
          width: 180px;
        }
      }
    }

    @media (max-width: 520px) {
      margin: 20px 0;

      &-details {
        flex-direction: column;

        span {
          font-size: 18px;
          margin-bottom: 10px;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      &-buttons {
        margin-top: 20px;

        .go-back-btn {
          margin-right: 5px;
          width: 160px;
        }

        .pay-btn {
          padding: 10px;
          width: 160px;
        }
      }
    }
  }

  &--empty {
    margin: 0 auto;
    width: 560px;
    text-align: center;

    h2 {
      font-size: 32px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 300px;
      margin: 45px auto 60px;
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }

    @media (max-width: 740px) {
      width: 340px;

      h2 {
        font-size: 26px;
      }

      p {
        font-size: 16px;
      }

      img {
        width: 200px;
      }

      .button--black {
        font-size: 16px;
        width: 200px;
      }
    }

    @media (max-width: 520px) {
      width: 280px;

      h2 {
        font-size: 24px;
      }

      p {
        font-size: 14px;
      }

      .button--black {
        font-size: 14px;
        width: 180px;
      }
    }
  }
}
