.root {
  position: relative;
}

.input {
  width: 100%;
  max-width: 400px;
  padding: 10px 45px;
  border-radius: 15px;
  border: 3px solid rgba(0, 0, 0, 0.5);
  font-size: 22px;

  &:focus {
    border: 3px solid rgba(0, 0, 0, 0.6);
  }
}

.searchIcon,
.clearIcon {
  position: absolute;
  top: 18px;
  width: 18px;
  height: 18px;
  opacity: 0.5;
}

.searchIcon {
  left: 15px;
}

.clearIcon {
  right: 15px;
  cursor: pointer;
}

@media (max-width: 1050px) {
  .input {
    font-size: 18px;
  }
}

@media (max-width: 730px) {
  .input {
    margin-top: 20px;
    font-size: 18px;
  }

  .searchIcon,
  .clearIcon {
    top: 35px;
  }
}
